import React from 'react';
import '../css/DashBoard.scss';
import ExportData from './ExportData';

/*
  DashBoard component to show the dashboard page
*/
class DashBoard extends React.Component{
    /*
     constructor to inialize the state variables of the signup page
   */
    constructor(props){
        super(props);
        this.state={
          cashInDetails : [
            {
              details : {name:"Mani",address:"Salem",email:"mani@gmail.com",phonenumber:9715848142},
              individualDetails : [{date:"11/08/2020",amount:1234},{date:"12/08/2020",amount:2000},
                                   {date:"11/08/2020",amount:1234},{date:"12/08/2020",amount:2000},
                                   {date:"11/08/2020",amount:1234},{date:"12/08/2020",amount:2000}]
           },
           {
            details : {name:"Elango",address:"Chennai",email:"elango@gmail.com",phonenumber:9715848144},
            individualDetails : [{date:"13/08/2020",amount:5000},{date:"16/08/2020",amount:3000},
                                 {date:"11/08/2020",amount:1234},{date:"12/08/2020",amount:2000},
                                 {date:"11/08/2020",amount:1234},{date:"12/08/2020",amount:2000}]
           },
          ]
        }
        this.handleDivClick = this.handleDivClick.bind(this);
    }
    handleDivClick(event){
      const ele = document.getElementById(`${event.target.id}_view`);
      if(ele.style.display=="block")
      ele.style.display = "none";
      else
      ele.style.display = "block";
    }
    /*
     This method return a jsx in which our actual elements are displayed in the screen
     This method invoked by default when we use this component(DashBoard class)
     Invoked whenever the state of this component is changed
    */
    render(){
        return(
            <div className="dashboard">
              <ExportData/>
                <section className = "CashIn_Details_Scroll SectionPadding">
                  <section className = "CashIn_Details_Main">
                     <h1 className="CashIn_Details_Title">CashIn Details</h1>
                     <section className="CashIn_Details_Wrapper">
                        <div>
                          <section className = "CashIn_Details_Wrapper_Content">
                            <div className="GridHeader_header" id="div1">
                              <div className="GridColumn_column" style={{flexGrow:0,flexShrink:0,flexBasis:80}}>
                                <div className="GridColumn_value">Name</div>
                              </div>
                              <div className="GridColumn_column" style={{flexGrow:0,flexShrink:0,flexBasis:80}}>
                                <div className="GridColumn_value">Address</div>
                              </div>
                              <div className="GridColumn_column" style={{flexGrow:0,flexShrink:0,flexBasis:110}}>
                                <div className="GridColumn_value">Email</div>
                              </div>
                              <div className="GridColumn_column" style={{flexGrow:0,flexShrink:0,flexBasis:150}}>
                                <div className="GridColumn_value">Phone Number</div>
                              </div>
                            </div>
                            {
                              this.state.cashInDetails.map((items,index)=>{
                                return (
                                <div className="GridRecord" id={`record_${index+1}`}><div id={`record_${index+1}`} className="GridRow_Row" onClick = {this.handleDivClick}>
                              <div id={`record_${index+1}`} className="GridColumn_column" style={{flexGrow:0,flexShrink:0,flexBasis:80}}>
                              <div id={`record_${index+1}`} className="GridColumn_value">{items.details.name}</div>
                              </div>
                              <div id={`record_${index+1}`} className="GridColumn_column" style={{flexGrow:0,flexShrink:0,flexBasis:80}}>
                                <div id={`record_${index+1}`} className="GridColumn_value">{items.details.address}</div>
                              </div>
                              <div id={`record_${index+1}`} className="GridColumn_column" style={{flexGrow:0,flexShrink:0,flexBasis:110}}>
                                <div id={`record_${index+1}`} className="GridColumn_value">{items.details.email}</div>
                              </div>
                              <div id={`record_${index+1}`} className="GridColumn_column" style={{flexGrow:0,flexShrink:0,flexBasis:150}}>
                                <div id={`record_${index+1}`} className="GridColumn_value">{items.details.phonenumber}</div>
                              </div>
                            </div>
                            <div id={`record_${index+1}_view`} style={{display:"none"}}>
                            { items.individualDetails.map((inItems)=>{
                              return (
                                <div className="GridRecord_View">
                              <div className="GridColumn_column" style={{flexGrow:0,flexShrink:0,flexBasis:100}}>
                              <div className="GridColumn_value">{inItems.date}</div>
                              </div>
                              <div className="GridColumn_column" style={{flexGrow:0,flexShrink:0,flexBasis:80}}>
                                <div className="GridColumn_value">{inItems.amount}.Rs</div>
                              </div>
                            </div>
                              )
                            })
                              }
                              </div>
                            </div>
                            )
                              })
                            }
                          </section>
                        </div>
                     </section>
                  </section>
                </section>
            </div>
        )
    }
}

/*
  Exporting the dashboard component such that any component can use this component by importing this component
*/
export default DashBoard;